@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8c4ef;
  color: #1b1919;
  line-height: 1.5;
  margin: 0;
  padding: 30px;
}

#search-box {
  font-size: 20px;
  font-weight: 100;
  padding: 15px;
  max-width: 320px;
  margin-bottom: 24px;
  margin-left: 40px;
  border: none;
  border-radius: 15px;
}

#search-box:focus {
  outline: none;
}

.country-list {
  list-style: none;
}

.country-list li {
  display: flex;
  padding-bottom: 10px;

  font-size: 22px;
  font-weight: 500;
}

.country-list img {
  margin-right: 10px;
}

.country-info {
  margin-left: 50px;
}

.country-info img {
  margin-right: 10px;
}

.country-info h1 {
  display: flex;
  align-items: center;
  margin-top: 0;
  font-size: 40px;
}

.country-info p {
  font-size: 22px;
  font-weight: 500;
}
